<template>
	<div class="sceneFirst">
		<div class="sceneFirstTable">
			<!-- <div class="sceneFirstTableBtn">
				<Button type="primary" custom-icon="iconfont icon-xinzeng" @click="sF_TableAdd"></Button>
				<Button type="primary" icon="ios-create-outline" @click="sF_TableAction('edit')"></Button>
				<Button type="warning" custom-icon="iconfont icon-shanchu" @click="sF_TableAction('del')"></Button>
				<Button type="primary" custom-icon="iconfont icon-baogao" @click="sF_TableAction('word')"></Button>
			</div> -->
			<Table ref="sF_Table" :columns="sF_TableHeard" :data="sF_TableData" stripe>
				<template slot-scope="{ row, index }" slot="rwState">
					<span v-if="row.d10=='1'" style="color:#4b85fa"></span>
					<span v-else style="color: #dbdbdb;"></span>
				</template>
				<template slot-scope="{ row, index }" slot="action">
					<Button type="error" size="small">()</Button>
				</template>
			</Table>
		</div>
		<!-- <div class="sceneFirstPage">
			<Page :total="100" show-elevator show-total />
		</div> -->
		<Modal v-model="sF_Modal">
			<p slot="header" style="color:#4b85fa;background: #f5f7fa;">
				<span>{{sF_ModalTitle}}</span>
			</p>
			<Form ref="sF_FromData" label-position="top" :model="sF_FromData" :rules="sF_FromRule">
				<FormItem label=":" prop="rwName">
					<Input v-model="sF_FromData.rwName" placeholder=""></Input>
				</FormItem>
				<FormItem label=":" prop="rwDate">
					<DatePicker type="date" placeholder="" style="width: 100%;" v-model="sF_FromData.rwDate">
					</DatePicker>
				</FormItem>
				<FormItem label=":">
					<Select placeholder="" @on-change="changeExpert" label-in-value>
						<Option v-for="item in expert" :value="item.id" :label="item.name" :key="item.id">
							<span>{{item.name}}</span>
							<span style="float:right;color:#ccc">{{item.major}}</span>
						</Option>
					</Select>
				</FormItem>
				<div class="addFromExpert">
					<Tag v-for="i in expertList" :key="i.value" :name="i.label" closable @on-close="tagClose">
						{{ i.label }}
					</Tag>
				</div>
				<Row :gutter="16">
					<Col span="12">
					<FormItem label=":" prop="yxCompany">
						<Input v-model="sF_FromData.yxCompany" placeholder=""></Input>
					</FormItem>
					</Col>
					<Col span="12">
					<FormItem label=":" prop="jgCompany">
						<Input v-model="sF_FromData.jgCompany" placeholder=""></Input>
					</FormItem>
					</Col>
					<Col span="12">
					<FormItem label=":" prop="sjCompany">
						<Input v-model="sF_FromData.sjCompany" placeholder=""></Input>
					</FormItem>
					</Col>
					<Col span="12">
					<FormItem label=":" prop="sgCompany">
						<Input v-model="sF_FromData.sgCompany" placeholder=""></Input>
					</FormItem>
					</Col>
					<Col span="12">
					<FormItem label=":" prop="glCompany">
						<Input v-model="sF_FromData.glCompany" placeholder=""></Input>
					</FormItem>
					</Col>
					<Col span="12">
					<FormItem label=":" prop="wzCompany">
						<Input v-model="sF_FromData.wzCompany" placeholder=""></Input>
					</FormItem>
					</Col>
				</Row>
			</Form>
			<div slot="footer" style="text-align: center;">
				<Button type="primary" ghost custom-icon="iconfont icon-166991" @click="sF_FromSave"></Button>
				<Button type="primary" icon="ios-paper-plane-outline" @click="sF_FromSub('sF_FromData')"></Button>
				<Button custom-icon="iconfont icon-chehui"></Button>
			</div>
		</Modal>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				sF_ModalTitle: '',
				sF_Modal: false,
				sF_FromData: {
					rwName: '',
					rwDate: '',
					yxCompany: '',
					jgCompany: '',
					sjCompany: '',
					sgCompany: '',
					glCompany: '',
					wzCompany: ''
				},
				sF_FromRule: {
					rwName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					rwDate: [{
						required: true,
						type: 'date',
						message: '',
						trigger: 'change'
					}],
					yxCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					jgCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					sjCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					glCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					sgCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					wzCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
				},
				sF_TableHeard: [{
						title: '',
						align: 'center',
						key: 'checkBox',
						width: 80,
						render: (h, params) => {
							var _this = this;
							return h('div', [
								h('Checkbox', {
									props: {
										value: params.row.checkBox
									},
									on: {
										'on-change': (e) => {
											if (e == false) {
												_this.disabled = true
											}
											_this.sF_TableData.forEach((items) => {
												_this.$set(items, 'checkBox', false)
											});
											_this.sF_TableData[params.index].checkBox = e;
										}
									}
								})
							])
						}
					}, {
						type: 'index',
						align: 'center'
					},
					{
						title: '',
						key: 'd1',
						width: 180,
					},
					{
						title: '',
						key: 'd2',
						width: 300,
					},
					{
						title: '',
						key: 'd3'
					},
					{
						title: '',
						key: 'd4',
						width: 150,
					},
					{
						title: '',
						key: 'd5',
						width: 150,
					},
					{
						title: '',
						key: 'd6',
						width: 150,
					},
					{
						title: '',
						key: 'd7'
					},
					{
						title: '',
						key: 'd8',
						width: 150,
					},
					{
						title: '',
						key: 'd9'
					},
					{
						title: '',
						key: 'd10'
					}
				],
				sF_TableData: [{
					d1: '[] 202105001 ',
					d2: '10,',
					d3: '',
					d4: '2021-05-22 12:00',
					d5: '2021-05-22 12:30',
					d6: '2021-05-22 16:30',
					d7: '',
					d8: '-10kV -.pdf',
					d9: '',
					d10: ''
				}, {
					d1: '[] 202105002 ',
					d2: '10,',
					d3: '',
					d4: '2021-05-22 12:00',
					d5: '2021-05-22 12:30',
					d6: '2021-05-22 16:30',
					d7: '',
					d8: '-10kV -.pdf',
					d9: '',
					d10: ''
				}]
			}

		},
		methods: {
			// 
			sF_TableAdd() {
				this.sF_ModalTitle = '';
				this.sF_Modal = true;
			},
			// 
			sF_TableAction(type) {
				let sTD = this.sF_TableData;
				var c;
				for (var i = 0; i < sTD.length; i++) {
					if (sTD[i].checkBox) {
						c = sTD[i]
					}
				}
				if (c == undefined) {
					this.errMsg('')
				} else {
					if (type == 'edit') {
						this.sF_ModalTitle = '';
						this.sF_Modal = true;
					} else if (type == 'del') {
						this.$Modal.confirm({
							title: '',
							content: '',
							onOk: () => {
								this.$Message.info('');
							},
							onCancel: () => {
								this.$Message.info('');
							}
						});
					} else if (type == 'word') {
						const msg = this.$Message.loading({
							content: '...',
							duration: 0
						});
						setTimeout(msg, 3000);
					}

				}

			},
			// 
			// value(item)
			changeExpert(value) {
				this.expertList.push(value)
			},
			// 
			// name
			tagClose(event, name) {
				const index = this.expertList.indexOf(name);
				this.expertList.splice(index, 1);
			},
			// 
			sF_FromSave() {
				this.successMsg('!')
				this.sF_Modal = false;
				// console.log(this.sF_FromData)
			},
			// 
			// fromNameref
			sF_FromSub(fromName) {
				this.$refs[fromName].validate((valid) => {
					if (valid) {
						this.successMsg('!')
						this.sF_Modal = false;
					} else {
						this.errMsg('!')
					}
				})
			},
			// 
			// title
			successMsg(title) {
				this.$Message.success(title)
			},
			// 
			// err
			errMsg(err) {
				this.$Message.error(err)
			},
		}
	}
</script>

<style>
	.screenLine {
		margin-left: 10px;
		margin-right: 10px;
	}

	.sceneFirstScreenInner {
		background-color: #fafafa;
		border: 1px solid #dce0e6;
	}

	.sceneFirstScreenInner .ivu-form-item {
		margin-top: 15px;
		margin-bottom: 15px;
	}

	.sceneFirstTable {
		margin-top: 20px;
	}

	.sceneFirstTableBtn {
		margin-bottom: 20px;
	}

	.sceneFirstTableBtn button {
		margin-right: 10px;
	}

	.sceneFirstPage {
		margin-top: 50px;
		text-align: center;
	}

	.addFromExpert {
		width: 100%;
		min-height: 60px;
		border: 1px solid #dbdbdb;
		padding: 5px;
		margin-bottom: 16px;
	}
</style>
