<template>
	<div class="sceneProblem">
		<div class="sceneProblemTable">
			<div class="sceneProblemTableBtn">
				<!-- <Button type="primary" custom-icon="iconfont icon-xinzeng" @click="sP_TableAdd"></Button>
				<Button type="primary" icon="ios-create-outline" @click="sP_TableAction('edit')"></Button>
				<Button type="warning" custom-icon="iconfont icon-shanchu" @click="sP_TableAction('del')"></Button>
				<Button type="primary" custom-icon="iconfont icon-tijiao" ghost
					@click="sP_TableAction('sub')"></Button>
				<Button type="primary" custom-icon="iconfont icon-chehui" ghost
					@click="sP_TableAction('back')"></Button> -->
				<Button type="primary" icon="ios-paper-plane-outline" ghost
					@click="sP_TableAction('before')"></Button>
				<Button type="primary" icon="ios-paper-plane-outline" ghost
					@click="sP_TableAction('tell')"></Button>
			</div>
			<Table ref="currentRowTable" :columns="sP_TableHeard" :data="sP_TableData" stripe>

			</Table>
		</div>
		<div class="sceneProblemPage">
			<Page :total="100" show-elevator show-total />
		</div>
		<Modal v-model="sP_Modal">
			<p slot="header" style="color:#4b85fa;background: #f5f7fa;">
				<span>{{sP_ModalTitle}}</span>
			</p>
			<Form ref="sP_FromData" label-position="top" :model="sP_FromData" :rules="sP_FromRule">
				<FormItem label=":" prop="rwName">
					<Input v-model="sP_FromData.rwName" placeholder=""></Input>
				</FormItem>
				<Row :gutter="16">
					<Col span="12">
					<FormItem label=":" prop="yxCompany">
						<Select v-model="sP_FromData.yxCompany" style="width:100%" placeholder="">
							<Option value="1"></Option>
							<Option value="2"></Option>
							<Option value="3"></Option>
						</Select>
					</FormItem>
					</Col>
					<Col span="12">
					<FormItem label=":" prop="jgCompany">
						<Select v-model="sP_FromData.yxCompany" style="width:100%" placeholder="">
							<Option value="1">1</Option>
							<Option value="2">2</Option>
							<Option value="3">3</Option>
						</Select>
					</FormItem>
					</Col>
				</Row>
				<FormItem label=":" prop="wtms">
					<Input v-model="sP_FromData.wtms" type="textarea" :autosize="{minRows: 2,maxRows: 10}"
						placeholder=""></Input>
				</FormItem>
				<FormItem label=":" prop="wfxz">
					<Cascader :data="data1" v-model="sP_FromData.wfxz"></Cascader>
				</FormItem>
				<FormItem label=":" prop="zgyj">
					<Input v-model="sP_FromData.zgyj" type="textarea" :autosize="{minRows: 2,maxRows: 10}"
						placeholder=""></Input>
				</FormItem>
				<FormItem label=":">
					<div class="demo-upload-list" v-for="item in uploadList">
						<template v-if="item.status === 'finished'">
							<img :src="item.url">
							<div class="demo-upload-list-cover">
								<Icon type="ios-eye-outline" @click.native="handleView(item.name)"></Icon>
								<Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
							</div>
						</template>
						<template v-else>
							<Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
						</template>
					</div>
					<Upload ref="upload" :show-upload-list="false" :default-file-list="defaultList"
						:on-success="handleSuccess" :format="['jpg','jpeg','png']" :max-size="2048"
						:on-format-error="handleFormatError" :on-exceeded-size="handleMaxSize"
						:before-upload="handleBeforeUpload" multiple type="drag"
						action="//jsonplaceholder.typicode.com/posts/" style="display: inline-block;width:58px;">
						<div style="width: 58px;height:58px;line-height: 58px;">
							<Icon type="ios-camera" size="20"></Icon>
						</div>
					</Upload>
					<Modal title="" v-model="visible" :closable="false">
						<img :src="'https://o5wwk8baw.qnssl.com/' + imgName + '/large'" v-if="visible"
							style="width: 100%">
					</Modal>
				</FormItem>
			</Form>
			<div slot="footer" style="text-align: center;">
				<Button type="primary" ghost icon="ios-search" @click="sP_FromSave"></Button>
				<Button type="primary" icon="ios-paper-plane-outline" @click="sP_FromSub('sP_FromData')"></Button>
			</div>
		</Modal>
		<Modal v-model="sP_AlarmModal" footer-hide>
			<p slot="header" style="color:#4b85fa;background: #f5f7fa;">
				<span>{{sP_AlarmModalTitle}}</span>
			</p>
			<Form :model="sP_AlarmData" :label-width="100">
				<FormItem label=":">
					<span>[]202105001</span>
				</FormItem>
				<FormItem label=":">
					 <Input v-model="sP_AlarmData.wtms" type="textarea" :autosize="{minRows: 4,maxRows: 10}" placeholder=""></Input>
				</FormItem>
				<FormItem label=":">
					<Button icon="md-add" type="primary" ghost></Button>
					<Input v-model="sP_AlarmData.fj" clearable />
				</FormItem>
				<FormItem label=":">
					<DatePicker type="datetime" format="yyyy-MM-dd HH:mm" placeholder="" style="width: 100%;"></DatePicker>
				</FormItem>
				<FormItem label=":">
					<Input v-model="sP_AlarmData.zgr" placeholder="" />
				</FormItem>
				<FormItem >
					<Button type="primary" icon="ios-paper-plane-outline"></Button>
				</FormItem>
			</Form>
		</Modal>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				sP_AlarmModal: false,
				sP_AlarmModalTitle: '',
				sP_AlarmData: {
					bh:'',
					wtms: '10,',
					yqfksj: '',
					zgr: '',
					fj:'--[]202105001.docx'
				},
				defaultList: [{
						'name': 'a42bdcc1178e62b4694c830f028db5c0',
						'url': 'https://img-pre.ivsky.com/img/tupian/pre/202012/08/fengche-007.jpg'
					},
					{
						'name': 'bc7521e033abdd1e92222d733590f104',
						'url': 'https://img-pre.ivsky.com/img/tupian/pre/202012/08/fengche-007.jpg'
					}
				],
				imgName: '',
				visible: false,
				uploadList: [],
				data1: [{
					value: '01',
					label: '',
					children: [{
						value: '0101',
						label: '',
						children: [{
							value: '010101',
							label: ''
						}, {
							value: '010102',
							label: ''
						}]
					}, {
						value: '0102',
						label: ''
					}, {
						value: '0103',
						label: ''
					}]
				}, {
					value: '02',
					label: '',

				}],
				sP_ModalTitle: '',
				expertList: [{
					label: '',
					value: '0'
				}],
				expert: [{
					name: '',
					major: 'XXXX',
					id: '1'
				}, {
					name: '',
					major: 'XXXX',
					id: '2'
				}, {
					name: '',
					major: 'XXXX',
					id: '3'
				}],
				sP_Modal: false,
				sP_FromData: {
					rwName: '',
					rwDate: '',
					yxCompany: '',
					jgCompany: '',
					sjCompany: '',
					sgCompany: '',
					glCompany: '',
					wzCompany: '',
					wtms: '',
					wfxz: [],
					zgyj: ''
				},
				sP_FromRule: {
					rwName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					rwDate: [{
						required: true,
						type: 'date',
						message: '',
						trigger: 'change'
					}],
					yxCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					jgCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					sjCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					glCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					sgCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					wzCompany: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
				},
				sP_TableHeard: [{
						title: '',
						align: 'center',
						key: 'checkBox',
						width: 80,
						render: (h, params) => {
							var _this = this;
							return h('div', [
								h('Checkbox', {
									props: {
										value: params.row.checkBox
									},
									on: {
										'on-change': (e) => {
											if (e == false) {
												_this.disabled = true
											}
											_this.sP_TableData.forEach((items) => {
												_this.$set(items, 'checkBox', false)
											});
											_this.sP_TableData[params.index].checkBox = e;
										}
									}
								})
							])
						}
					}, {
						type: 'index',
						width: 60,
						align: 'center'
					}, {
						title: '',
						key: 'd1',
						width: 220,
					}, {
						title: '',
						key: 'd2'
					}, {
						title: '',
						key: 'd3'
					}, {
						title: '',
						key: 'd4',
						width: 300,
					}, {
						title: '',
						key: 'd5',
						width: 300,
						tooltip: true,
						tooltipTheme: 'light'
					}, {
						title: '',
						key: 'd6',
						width: 300,
					}, {
						title: '',
						key: 'd7'
					},

				],
				sP_TableData: [{
					d1: '-10kV-1',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-2',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-3',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-4',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-5',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}, {
					d1: '-10kV-',
					d2: '',
					d3: '',
					d4: '10,',
					d5: '',
					d6: '110',
					d7: '1.png',
				}]
			}

		},
		methods: {
			// 
			sP_TableAdd() {
				this.sP_ModalTitle = '';
				this.sP_Modal = true;
			},
			// 
			sP_TableAction(type) {
				let sTD = this.sP_TableData;
				var c;
				for (var i = 0; i < sTD.length; i++) {
					if (sTD[i].checkBox) {
						c = sTD[i]
					}
				}
				if (c == undefined) {
					this.errMsg('')
				} else {
					if (type == 'edit') {
						this.sP_ModalTitle = '';
						this.sP_Modal = true;
					} else if (type == 'del') {
						this.$Modal.confirm({
							title: '',
							content: '',
							onOk: () => {
								this.$Message.info('');
							},
							onCancel: () => {
								this.$Message.info('');
							}
						});
					} else if (type == 'before') {
						this.sP_AlarmModalTitle = '';
						this.sP_AlarmModal = true;
					}

				}

			},
			// 
			// value(item)
			changeExpert(value) {
				this.expertList.push(value)
			},
			// 
			// name
			tagClose(event, name) {
				const index = this.expertList.indexOf(name);
				this.expertList.splice(index, 1);
			},
			// 
			sP_FromSave() {
				this.successMsg('!')
				this.sP_Modal = false;
				// console.log(this.sP_FromData)
			},
			// 
			// fromNameref
			sP_FromSub(fromName) {
				this.$refs[fromName].validate((valid) => {
					if (valid) {
						this.successMsg('!')
						this.sP_Modal = false;
					} else {
						this.errMsg('!')
					}
				})
			},
			// 
			// title
			successMsg(title) {
				this.$Message.success(title)
			},
			// 
			// err
			errMsg(err) {
				this.$Message.error(err)
			},
			handleView(name) {
				this.imgName = name;
				this.visible = true;
			},
			handleRemove(file) {
				const fileList = this.$refs.upload.fileList;
				this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
			},
			handleSuccess(res, file) {
				file.url = 'https://o5wwk8baw.qnssl.com/7eb99afb9d5f317c912f08b5212fd69a/avatar';
				file.name = '7eb99afb9d5f317c912f08b5212fd69a';
			},
			handleFormatError(file) {
				this.$Notice.warning({
					title: 'The file format is incorrect',
					desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
				});
			},
			handleMaxSize(file) {
				this.$Notice.warning({
					title: 'Exceeding file size limit',
					desc: 'File  ' + file.name + ' is too large, no more than 2M.'
				});
			},
			handleBeforeUpload() {
				const check = this.uploadList.length < 5;
				if (!check) {
					this.$Notice.warning({
						title: 'Up to five pictures can be uploaded.'
					});
				}
				return check;
			}
		},
		mounted() {
			this.uploadList = this.$refs.upload.fileList;
		}
	}
</script>

<style>
	.demo-upload-list {
		display: inline-block;
		width: 60px;
		height: 60px;
		text-align: center;
		line-height: 60px;
		border: 1px solid transparent;
		border-radius: 4px;
		overflow: hidden;
		background: #fff;
		position: relative;
		box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
		margin-right: 4px;
	}

	.demo-upload-list img {
		width: 100%;
		height: 100%;
	}

	.demo-upload-list-cover {
		display: none;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0, 0, 0, .6);
	}

	.demo-upload-list:hover .demo-upload-list-cover {
		display: block;
	}

	.demo-upload-list-cover i {
		color: #fff;
		font-size: 20px;
		cursor: pointer;
		margin: 0 2px;
	}

	.sceneProblemTable {
		margin-top: 20px;
	}

	.sceneProblemTableBtn {
		margin-bottom: 20px;
	}

	.sceneProblemTableBtn button {
		margin-right: 10px;
	}

	.sceneProblemPage {
		margin-top: 50px;
		text-align: center;
	}

	.addFromExpert {
		width: 100%;
		min-height: 60px;
		border: 1px solid #dbdbdb;
		padding: 5px;
		margin-bottom: 16px;
	}
</style>
